import React from "react";
import { useParams } from "react-router-dom";
import { getProjectRelativePath } from "kit";
import "./index.less";
import xhtz from "@/assets/img/youhualei/xhtz.png";
import lkqh from "@/assets/img/youhualei/lkqh.png";
import jtyd from "@/assets/img/youhualei/jtyd.png";
import gjxl from "@/assets/img/youhualei/gjxl.png";
const obj = {
  xhtz,
  lkqh,
  jtyd,
  gjxl,
};
function Optimize() {
  const previewParams = useParams();
  const picName = previewParams.name;
  return (
    <div className="optimize-wrap">
      <img src={obj[picName]} />
    </div>
  );
}

export default Optimize;
